import EasyHttpAxios from "easy-http-axios";
import {
    Base64
} from "js-base64";
import EasyHttp from "easy-http";

EasyHttp.use(EasyHttpAxios);

export const Requester1 = new EasyHttp("https://miniptapi.innourl.com/Redpacket", {
    GetUserPlayInfo: {
        u: "/User/GetUserPlayInfo"
    }
}).addProcessor(e => {
    return Base64.encode(encodeURI(e));
});

export const Requester2 = new EasyHttp("https://miniptapi.innourl.com/Redpacket", {
    GetUserPlayInfo: {
        u: "/User/GetUserPlayInfo/{userId:p}&{brandId}",
        h: 1
    },
    GetUserPlayInfo2: {
        u: "/User/GetUserPlayInfo/{userId}&{brandId}",
        d: ":p"
    }
}).bindDictate("p", e => {
    return Base64.encode(encodeURI(e));
});